@media screen and (min-width: 375px) {
  .work-list h4 {
    font-weight: bold;
    margin-bottom: 5px;
    padding-top: 5px;
  }
  .work-info {
    min-width: 200px;
    cursor: pointer;
    border-top: 2px solid transparent;
    margin-bottom: 6%;
  }

  .work-info:hover {
    border-top: 2px solid rgb(248, 217, 15);
  }
  .thumbnail {
    padding: 5px 0;
  }
  .thumbnail img {
    width: 100%;
    object-fit: cover;
  }

  .work-detail {
    z-index: 11;
    width: 85%;
    height: 90%;
    position: fixed;
    top: 7%;
    left: 7.5%;
    padding: 10px;
    background-color: #fff;
    border: 1px solid black;
    box-shadow: 0 -2em 2em rgba(0, 0, 0, 0.2), 0 0 0 1px white,
      0.5em 1em 2em rgba(0, 0, 0, 0.2);
    overflow: auto;
    scrollbar-color: rgb(209, 209, 209);
    scrollbar-width: thin;
  }

  .work-title {
    font-size: 20px;
    width: 100%;
  }
  .work-detail {
    display: flex;
    flex-direction: column;
  }
  .work-img {
    width: 100%;
    margin-top: 20px;
  }
  .close {
    display: block;
    position: absolute;
    top: 2%;
    right: 3%;
    width: 30px;
    height: 30px;
    cursor: pointer;
    background: no-repeat center url(../assets/Close_square_light@2x.png);
  }
}

@media screen and (min-width: 768px) {
  .thumbnail img {
    width: 80%;
  }
}

@media screen and (min-width: 1024px) {
  .work-list {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: stretch;
    align-content: center;
  }
  .work-info {
    min-width: 250px;
    margin-right: 2%;
    margin-bottom: 4%;
  }

  .work-detail img {
    max-width: 600px;
  }
  .thumbnail img {
    width: 250px;
  }
  .work-detail {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .work-title {
    width: 100%;
  }
  .work-img {
    width: 620px;
  }
  .work {
    width: 40%;
  }

  .work {
    justify-self: flex-end;
  }
  .close {
    top: 2%;
    right: 1.8%;
  }
}
