@media screen and (min-width: 375px) {
  .app-header {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    font-size: calc(10px + 1.5vmin);
    background-color: #fff;
    color: black;
    padding: 0 3%;
  }
  .header-menu {
    width: 50%;
    display: flex;
    justify-content: space-between;
    cursor: pointer;
  }
  .header-menu li {
    padding: 2%;
  }

  .header-menu li.active {
    border: 1px solid black;
    border-radius: 50px;
  }

  .default-message {
    font-size: calc(10px + 3vmin);
    text-align: center;
    margin: 50% auto 0;
  }
  .gif {
    width: 300px;
    margin-top: 15%;
  }
}

@media screen and (min-width: 768px) {
  .app-header {
    padding: 0 2%;
  }
  .header-menu {
    width: 40%;
  }
  .default-message {
    margin: 30% auto 0;
  }
  .gif {
    width: 600px;
    margin-top: 0;
    display: block;
    margin: 5% auto 0;
  }
}

@media screen and (min-width: 1024px) {
  .header-menu {
    width: 30%;
    max-width: 350px;
  }
  .default-message {
    font-size: calc(10px + 5vmin);
    margin: 20vh auto 0;
  }
}
