@media screen and (min-width: 375px) {
  .core-section {
    text-align: left;
    padding: 3%;
    margin-top: 12%;
  }
  .core-title {
    margin-bottom: 0.9vw;
  }

  .core {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    height: auto;
    padding: 2% 0;
  }
}

@media screen and (min-width: 768px) {
  .core-description {
    width: 70%;
  }
  .core-section {
    margin-top: 7%;
  }
}

@media screen and (min-width: 1024px) {
  .core-section {
    padding: 2%;
    margin-top: 7vh;
  }
  .core-title {
    margin-bottom: 0;
  }
}
